@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Lato", sans-serif;
}

body {
  background-color: #1d1e24;
  color: #f5f5f5;
}

.home,
.reports,
.products {
  display: flex;
  height: 90vh;
  align-items: center;
  justify-content: center;
  font-size: 3rem;
}

.navbar {
  background-color: #121212;
  height: 80px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.menu-bars {
  margin-left: 2rem;
  font-size: 2rem;
  background: none;
}

.menu-bars :hover {
  fill: #911111;
}

.nav-menu {
  background-color: #121212;
  width: 250px;
  height: 100vh;
  display: flex;
  justify-content: center;
  position: fixed;
  top: 0;
  left: -100%;
  transition: 850ms;
}

.nav-menu.active {
  left: 0;
  transition: 350ms;
}

.nav-text {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 8px 0px 8px 16px;
  list-style: none;
  height: 60px;
}

.nav-text a {
  text-decoration: none;
  color: #f5f5f5;
  font-size: 18px;
  width: 95%;
  height: 100%;
  display: flex;
  align-items: center;
  padding: 0 16px;
  border-radius: 4px;
}

.nav-text a:hover {
  background-color: #911111;
}

.nav-menu-items {
  width: 100%;
  border-radius: 10px;
}

.navbar-toggle {
  background-color: #121212;
  width: 100%;
  height: 80px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

span {
  margin-left: 16px;
}

* {
  -webkit-animation: fadein 1s;
}

@-webkit-keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.spell-search-box {
  -webkit-animation: fadein 4s;
  text-align: center;
}

.spell-search {
  margin-top: 25px;
  height: 25px;
  min-width: 500px;
  min-height: 50px;
  border-radius: 50px;
  text-align: center;
  font-size: 25px;
  border: 1px solid #121212;
  align-content: center;
}

.spell-search:hover {
  border-color: #911111;
  box-shadow: 1px 1px 5px #911111;
  outline: #911111;
}

.spell-search:focus {
  border-color: #911111;
  box-shadow: 0 0 5px #911111;
  outline: #911111;
}

.spell-details {
  text-align: center;
  margin-top: 50px;
  margin-left: 500px;
  margin-right: 500px;
  padding: 10px 50px 25px 50px;
  background-color: #121212;
  border-radius: 50px;
}

.spell-info-box {
  display: flex;
  margin-left: 490px;
  margin-right: 490px;
}

.spell-data-box {
  flex: 1 1;
  text-align: center;
  margin-top: 50px;
  margin-left: 10px;
  margin-right: 10px;
  padding: 8px;
  background-color: #121212;
  border-radius: 50px;
}

.spell-range {
  text-align: center;
  align-content: center;
  justify-content: center;
  align-items: center;
}

.spell-desc {
  margin-top: 20px;
}

.spell-higher-level {
  margin-top: 20px;
}

.slider-box {
  text-align: center;
  margin-top: 50px;
  margin-left: 500px;
  margin-right: 500px;
  padding: 10px 50px 50px 50px;
  background-color: #121212;
  border-radius: 50px;
}

.spell-damage {
  text-align: center;
  margin-top: 50px;
  min-width: 300px;
  margin-left: 1000px;
  margin-right: 1000px;
  padding: 50px;
  background-color: #121212;
  border-radius: 50px;
}

.spell-slider {
  margin-top: 25px;
}

.icons svg {
  font-size: 45px;
  align-content: left;
  border-radius: 50%;
  justify-content: space-between;
  margin-top: 20px;
  margin-left: 20px;
  margin-right: 20px;
}

.icons svg:hover {
  background: #911111;
  border-radius: 50%;
}

